import pokemonApi from "@/api/pokemonApi";

const getPokemon = () => {
  const pokemonArr = Array.from(Array(650));
 // console.log(pokemonArr);

  return pokemonArr.map((_, index) => index + 1);
};

const getPokemonOptions = async() => {
  const mixedPokemon = getPokemon().sort(() => Math.random() - 0.5);
 // console.log(mixedPokemon);

  const pokemon = await getPokemonNames(mixedPokemon.splice(0, 4));

  return pokemon;
};

const getPokemonNames = async ([a, b, c, d] = []) => {
    
    const promiseArr = [
        pokemonApi.get(`/${a}`),
        pokemonApi.get(`/${b}`),
        pokemonApi.get(`/${c}`),
        pokemonApi.get(`/${d}`)
    ]

    const [pa,pb,pc,pd] =  await Promise.all( promiseArr );

    return [
        {name: pa.data.name, id: pa.data.id},
        {name: pb.data.name, id: pb.data.id},
        {name: pc.data.name, id: pc.data.id},
        {name: pd.data.name, id: pd.data.id},
    ]
};

export default getPokemonOptions;
