<template>
  <div class="pokemonPic">
    <img class="hidden-pokemon" :src="imgSrc" alt="pokemon" />
    <img v-if="showPokemon" class="fade-in" :src="imgSrc" alt="pokemon" />
  </div>
</template>

<script>
export default {
  props: {
    pokemonId: {
      type: Number,
      required: true,
    },
    showPokemon: {
      required: true,
      default: false,
      type: Boolean,
    },
  },
  computed: {
    imgSrc() {
      return `https://raw.githubusercontent.com/PokeApi/sprites/master/sprites/pokemon/other/dream-world/${this.pokemonId}.svg`;
    },
  },
};
</script>

<style scoped>
.pokemonPic {
  height: 200px;
}
img {
  position: absolute;
  right: 50vw;
  transform: translateX(50%);
  height: 200px;
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
}
.hidden-pokemon {
  filter: brightness(0);
}
</style>
