<template>
  <img v-if="!pokemon" src=@/img/loading.gif>
  <div v-else>
    <h1>¿Cual es este pokémon?</h1>
    <PokemonPictures :pokemonId="pokemon.id" :showPokemon="showPokemon" />
    <PokemonOpciones v-if="!showedAnswer"
    :pokemon="pokemonArr" @selection="checkAnswer" />
    <div class="toShow fade-in" v-if="showAnswer">
      <h2>{{ message }}</h2>
      <button @click="newGame()">
        Nuevo juego
      </button>
    </div>    
  </div>
  
</template>

<script>
import PokemonOpciones from "@/components/PokemonOpciones";
import PokemonPictures from "@/components/PokemonPictures";

import getPokemonOptions from "@/helper/getPokemonOptions.js";
export default {
  components: {
    PokemonPictures,
    PokemonOpciones,
  },
  data() {
    return {
      pokemonArr: [],
      pokemon: null,
      showPokemon: false,
      showAnswer: false,
      message: '',
      showedAnswer: false
    }
  },
  methods: {
    async mixPokemonArray() {
      this.pokemonArr = await getPokemonOptions();

      const rndInt = Math.floor(Math.random() * 4 );
      this.pokemon = this.pokemonArr[ rndInt ];

    },
    checkAnswer(pokemonId){
      this.showedAnswer = true;
      this.showPokemon = true;
      if (pokemonId == this.pokemon.id){
        this.message = `¡Correcto, es ${this.pokemon.name}!`;
        this.showAnswer = true;
      } else {
        this.message = `Ooops... Era ${this.pokemon.name}`;
        this.showAnswer = true;
      }
    },
    newGame(){
      this.pokemonArr = [];
      this.pokemon = null;
      this.showPokemon = false;
      this.showAnswer = false;
      this.message = '';
      this.showedAnswer = false;
      this.mixPokemonArray();
    }
  }, 
  mounted(){
    this.mixPokemonArray();
  }
};
</script>

